import { Component, ElementRef, ViewChild } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  @ViewChild('recParent') 
  recParent?: ElementRef ;
  @ViewChild('trou') 
  trou?: ElementRef ;



  title = 'corn-hole-score';

  tour = "bleu";
  tourSiEgalite = "rouge";

  score : {bleu : number, rouge : number}[] = [];
  meneBleu : {point:number,top:number,left:number}[] = [];
  meneRouge : {point:number,top:number,left:number}[] = [];

  addLigneScore(){

    let scoreMeneBleu = 0 ;
    this.meneBleu.forEach(element => {
      scoreMeneBleu += element.point;
    });
    let scoreMeneRouge = 0 ;
    this.meneRouge.forEach(element => {
      scoreMeneRouge += element.point;
    });

   this.score.push({
     bleu : (scoreMeneBleu - scoreMeneRouge > 0 ? scoreMeneBleu - scoreMeneRouge : 0) + (this.score.length > 0 ? this.score[this.score.length-1].bleu : 0), 
    rouge : (scoreMeneRouge - scoreMeneBleu > 0 ? scoreMeneRouge - scoreMeneBleu : 0) + (this.score.length > 0 ?this.score[this.score.length-1].rouge: 0)
  });
  this.meneBleu  = [];
  this.meneRouge  = [];
  if(this.score[this.score.length-1].bleu == this.score[this.score.length-1].rouge) {
    this.tour =this.tourSiEgalite;
  } else{
    this.tour = this.score[this.score.length-1].bleu > this.score[this.score.length-1].rouge ? "bleu" : "rouge";
  }

  this.tourSiEgalite = this.tour == "bleu" ? "rouge" : "bleu";
}

getTotalMeneBleu(){
  let scoreMeneBleu = 0 ;
    this.meneBleu.forEach(element => {
      scoreMeneBleu += element.point;
    });
    return scoreMeneBleu;
}

getTotalMeneRouge(){
  let scoreMeneRouge = 0 ;
    this.meneRouge.forEach(element => {
      scoreMeneRouge += element.point;
    });
    return scoreMeneRouge;
}

getVirtuelScoreBleu(){
  return  (this.getTotalMeneBleu() - this.getTotalMeneRouge() > 0 ? this.getTotalMeneBleu() - this.getTotalMeneRouge() : 0) + (this.score.length > 0 ? this.score[this.score.length-1].bleu : 0);
}

getVirtuelScoreRouge(){
  return  (this.getTotalMeneRouge() - this.getTotalMeneBleu() > 0 ? this.getTotalMeneRouge() - this.getTotalMeneBleu() : 0) + (this.score.length > 0 ? this.score[this.score.length-1].rouge : 0);
}

getScoreBleu(){
  return  (this.score.length > 0 ? this.score[this.score.length-1].bleu : 0);
}

getScoreRouge(){
  return  (this.score.length > 0 ? this.score[this.score.length-1].rouge : 0);
}

  reverseScore(){
    let reverseScore = [];
    for (let i=this.score.length-1; i>= 0; i--){
      reverseScore.push(this.score[i]);
    }
    return reverseScore;
  }

  addPoint(event : MouseEvent,point:number){
    if(this.tour == "bleu"){
      this.addPointBleu(event ,point);
      this.tour = "rouge";
    } else {
      this.addPointRouge(event ,point);
      this.tour = "bleu"
    }
  }

  addPointBleu(event : MouseEvent,point:number){
    console.log(this.trou?.nativeElement.offsetLeft);
    console.log(event);
    event.stopPropagation();
    if(this.meneBleu.length<4){
      this.meneBleu.push({point:point,top:event.clientY,left:event.clientX});
    }
  }

  addPointRouge(event : MouseEvent,point:number){
    event.stopPropagation();
    if(this.meneRouge.length<4){
      this.meneRouge.push({point:point,top:event.clientY,left:event.clientX});
    }
  }

  noAction(event: MouseEvent){
    event.stopPropagation();
  }

  new(){
    Swal.fire({
      icon: 'question',
      text: "Êtes vous sûr ?",
      confirmButtonText: 'Oui',
      denyButtonText: 'Non',
      showDenyButton: true,
      showCloseButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        this.tour = "bleu";
   this. tourSiEgalite = "rouge";
  
    this.score = [];
    this.meneBleu  = [];
    this.meneRouge = [];
      }
    });
  }

  annuler(){
    if(this.tour == 'bleu'){
      if(this.meneRouge.length > 0){
        this.meneRouge.pop();
        this.tour = 'rouge';
      }
    } else {
      if(this.meneBleu.length > 0){
        this.meneBleu.pop();
        this.tour = 'bleu';
      }
    }

  }

  sacFall(event: MouseEvent,i:number,couleur:string){
    event.stopPropagation();
    if(couleur == 'bleu'){
      if(this.meneBleu[i].point == 1){
        Swal.fire({
          icon: 'question',
          confirmButtonText: 'Trou',
          denyButtonText: 'Out',
          showDenyButton: true,
          showCloseButton: true,
        }).then((result) => {
          if (result.isConfirmed) {
            this.meneBleu[i].point = 3;
            this.meneBleu[i].left = this.trou?.nativeElement.offsetLeft +120 - (i+1)*10;
            this.meneBleu[i].top = this.trou?.nativeElement.offsetTop +120- (i+1)*10 ;
            console.log("trou");
          } else  if (result.isDenied){
            this.meneBleu[i].point = 0;
            this.meneBleu[i].left = this.meneBleu[i].left > this.recParent?.nativeElement.offsetLeft +180 ? this.recParent?.nativeElement.offsetLeft+350 : this.recParent?.nativeElement.offsetLeft +20;
            console.log("Out");
          }
        }) ;
      }
    } else{
      if(this.meneRouge[i].point == 1){
        Swal.fire({
          icon: 'question',
          confirmButtonText: 'Trou',
          denyButtonText: 'Out',
          showDenyButton: true,
          showCloseButton: true,
        }).then((result) => {
          if (result.isConfirmed) {
            this.meneRouge[i].point = 3;
            this.meneRouge[i].left = this.trou?.nativeElement.offsetLeft+20 +(i+1)*10 ;
            this.meneRouge[i].top = this.trou?.nativeElement.offsetTop+30+ (i+1)*10 ;
            console.log("trou");
          } else  if (result.isDenied){
            this.meneRouge[i].point = 0;
            this.meneRouge[i].left = this.meneRouge[i].left > this.recParent?.nativeElement.offsetLeft+180 ? this.recParent?.nativeElement.offsetLeft+350 : this.recParent?.nativeElement.offsetLeft+20;
            console.log("Out");
          }
        }) ;
      }
  }
}

removeLastMene(){
  Swal.fire({
    icon: 'question',
    text: "Êtes vous sûr ?",
    confirmButtonText: 'Oui',
    denyButtonText: 'Non',
    showDenyButton: true,
    showCloseButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      this.score.pop();
    }
  });
}


}
